::-webkit-scrollbar {
  width: 0.7rem;
  background-color: #222;
}

::-webkit-scrollbar-thumb {
  border-radius: 0.5rem;
  background: #3b1bc9;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  padding-top: 8rem;
  font-family: "Nunito", sans-serif;
  background-color: #222;
  color: #fff2f2;
}
@media (min-width: 380px) {
  body {
    padding-top: 6rem;
  }
}

h1 {
  font-size: 38px;
}

h2 {
  font-size: 35px;
}

h3 {
  font-size: 28px;
}

h4 {
  font-size: 21px;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0;
}

img {
  width: 100%;
  height: 100%;
}

figure {
  margin: 0;
  padding: 0;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

a {
  color: #fff2f2;
  text-decoration: none;
}

blockquote {
  margin: 0;
}

section {
  margin-top: 2rem;
}

.container {
  width: 90%;
  max-width: 1400px;
  margin: 0 auto;
}

.page-title {
  font-size: 2rem;
}
.page-title span {
  text-transform: uppercase;
  color: #3b1bc9;
}

.sections {
  max-width: 1400px;
  margin: auto;
  padding: 2rem 0;
}
.sections-title {
  text-align: center;
  text-transform: uppercase;
  max-width: 35rem;
  font-weight: 900;
  line-height: 1.5;
}
.sections-title span {
  color: #3b1bc9;
}

.contact-us-btn, .check-out-btn {
  display: block;
  width: 50%;
  max-width: 15rem;
  margin: 1.5rem auto 0;
  padding: 0.8rem 1rem;
  text-align: center;
  font-weight: 700;
  background-color: #666;
  border-radius: 0.3rem;
  transition: all 0.3s ease-in-out;
}
.contact-us-btn:hover, .check-out-btn:hover {
  background-color: #7154f3;
  color: #fff2f2;
}

.link-important {
  color: #fff2f2;
  border: 0.1rem solid #fff2f2;
  border-radius: 0.5rem;
}

.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
  padding: 0.8rem 0;
  min-height: 4rem;
  background-color: #333;
}
.header .nav {
  display: flex;
  justify-content: space-around;
  gap: 0.5rem;
  align-items: center;
  flex-wrap: wrap;
}
@media (min-width: 380px) {
  .header .nav {
    justify-content: space-between;
  }
}
.header .nav-title {
  flex: 0 1 15rem;
}
.header .menu-responsive-icons {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 0.3rem;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
  border-radius: 50%;
  overflow: hidden;
  transition: transform 0.1s ease-in-out;
}
@media (min-width: 992px) {
  .header .menu-responsive-icons {
    display: none;
  }
}
.header .menu-responsive-icons:active {
  transform: scale(0.7);
}
.header .menu-responsive-icons span {
  display: block;
  width: 2rem;
  height: 0.5rem;
  background-color: #fff2f2;
}
.header .nav-links {
  position: absolute;
  top: 100%;
  left: -150%;
  width: 100%;
  max-width: 50rem;
  padding: 1rem;
  border-bottom-right-radius: 1rem;
  background-color: #333;
  transition: left 0.2s ease-in-out;
}
@media (min-width: 992px) {
  .header .nav-links {
    width: fit-content;
    position: static;
    flex: 0 0 fit-content;
    display: flex;
    padding: 0;
    gap: 0.5rem;
  }
}
.header .nav-links li {
  margin-bottom: 0.2rem;
  background-color: #222;
  border-radius: 0.2rem;
}
@media (min-width: 992px) {
  .header .nav-links li {
    margin: 0;
    background-color: transparent;
  }
}
.header .nav-links li:hover a {
  animation: animation_link_responsive ease-in-out 0.5s forwards;
}
@media (min-width: 992px) {
  .header .nav-links li:hover a {
    animation: none;
  }
}
.header .nav-links li a {
  display: block;
  padding: 1rem;
  text-transform: capitalize;
  font-weight: 700;
}
@media (min-width: 992px) {
  .header .nav-links li a {
    background-color: transparent;
    transition: all 0.5s ease-in-out;
  }
  .header .nav-links li a::after {
    content: "";
    position: absolute;
    bottom: 0;
    display: block;
    opacity: 0;
    height: 0.4rem;
    border-radius: 0.3rem;
    background-color: #3b1bc9;
  }
  .header .nav-links li a:hover::after {
    animation: after_links ease-in-out 0.5s forwards;
  }
}

.show-menu {
  left: 0 !important;
}

.active {
  text-shadow: 0 0 0.2rem #222, 0 0 1rem #fff2f2, 0 0 2rem #fff2f2, 0 0 2rem #fff2f2;
}

@keyframes after_links {
  0% {
    width: 0;
    opacity: 0;
  }
  100% {
    width: 3.5rem;
    opacity: 1;
  }
}
@keyframes animation_link_responsive {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
    background-color: #3b1bc9;
  }
}
.hero {
  width: 100%;
  overflow: hidden;
}

.home-slider {
  margin-top: 3rem;
}
.home-slider .swiper-slide {
  overflow: hidden;
  border-radius: 0.5rem;
  height: 30rem;
  width: 30rem;
}
.home-slider .swiper-slide img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.services .card-services-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
  gap: 1rem;
  max-width: 90rem;
  margin-top: 2rem;
}
.services .card-services {
  padding: 2rem;
  text-align: center;
  border-radius: 0.5rem;
  background-color: #333;
  transition: all 0.2s ease-in-out;
}
.services .card-services:hover {
  box-shadow: 0 0.3rem 0.4rem #666;
  transform: scaleY(1.05);
}
.services .card-services-img {
  width: 3rem;
  height: 3rem;
  margin: auto;
  padding: 0.3rem;
  border-radius: 50%;
  background-color: #3b1bc9;
}
.services .card-services-title {
  margin: 1rem 0;
  font-size: 2rem;
}
.services .card-services-text {
  font-weight: 300;
  font-weight: 1.5rem;
  line-height: 1.5;
}

.about-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 3rem;
  margin-top: 2rem;
}
.about-container .about-img {
  flex: 0 1 40rem;
  display: grid;
  margin: auto;
  outline: #666 solid 0.5rem;
  border-radius: 0.4rem;
}
.about-container .about-img img {
  object-fit: cover;
}
.about-container .about-details {
  flex: 1;
}
.about-container .about-details .about-title {
  font-size: 2.4rem;
}
.about-container .about-details p {
  margin-top: 1rem;
  line-height: 2;
}

.gallery-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));
  gap: 1rem;
  margin-top: 2rem;
  max-width: 60rem;
}
.gallery-container .gallery-details {
  position: relative;
  background-color: #333;
  overflow: hidden;
  border: 0.7rem solid #333;
  border-radius: 0.3rem;
}
.gallery-container .gallery-details .gallery-img {
  transition: all 0.3s ease-in-out;
  height: 12rem;
}
.gallery-container .gallery-details:hover .gallery-img {
  transform: scale(1.2);
  filter: brightness(0.6);
}
.gallery-container .gallery-details .gallery-title {
  font-size: 1.5rem;
  text-align: center;
  padding: 0.2rem 0;
  text-transform: capitalize;
}
.gallery-container .gallery-details .gallery-icons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding-top: 0.2rem;
}
.gallery-container .gallery-details .gallery-icons img {
  width: 1.5rem;
  height: 1.5rem;
}
.gallery-container .gallery-details .gallery-icons img:hover {
  filter: brightness(0.5);
}
.gallery-container .gallery-details .gallery-icons img:active {
  transform: scale(0.9);
}
.gallery-container .gallery-details .gallery-title,
.gallery-container .gallery-details .gallery-icons {
  position: absolute;
  z-index: 10;
  width: 100%;
  background-color: #333;
  transition: all 0.3s ease-in-out;
  visibility: hidden;
  opacity: 0;
}
.gallery-container .gallery-details .gallery-title {
  top: -3rem;
}
.gallery-container .gallery-details .gallery-icons {
  bottom: -3rem;
}
.gallery-container .gallery-details:hover .gallery-icons, .gallery-container .gallery-details:hover .gallery-title {
  visibility: visible;
  opacity: 1;
}
.gallery-container .gallery-details:hover .gallery-title {
  top: 0;
}
.gallery-container .gallery-details:hover .gallery-icons {
  bottom: 0;
}

.card-price-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(16rem, 1fr));
  gap: 1rem;
  max-width: 100rem;
  margin-top: 2rem;
}

.card-price {
  padding: 1.5rem 0;
  text-align: center;
  background-color: #333;
  border-radius: 0.5rem;
  transition: all 0.2s ease-in-out;
}
.card-price:hover {
  transform: scale(1.05);
  box-shadow: 0 0.3rem 0.3rem #666;
}
.card-price-title {
  padding: 1rem;
  text-transform: capitalize;
  background-color: #3b1bc9;
}
.card-price-price {
  margin: 2rem 0;
  font-size: 2.5rem;
  font-weight: 700;
}
.card-price-details {
  display: grid;
  gap: 1rem;
}
.card-price-details li {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.2rem;
  font-weight: 700;
  text-transform: capitalize;
}
.card-price-details li img {
  width: 2rem;
}

.swiper {
  width: 100%;
  height: 100%;
  margin-top: 2rem;
}

.card-review {
  padding: 1rem;
  border-radius: 0.3rem;
  background-color: #333;
}
.card-review-header {
  position: relative;
  display: flex;
  gap: 1rem;
  align-items: center;
}
.card-review-header::after {
  content: url("../images/quote.svg");
  position: absolute;
  right: 0;
  display: block;
  width: 3rem;
  height: 3rem;
}
.card-review-header-img {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  overflow: hidden;
}
.card-review-header-client h3 {
  font-size: 1.6rem;
}
.card-review-header-client p {
  font-size: 1rem;
  font-weight: 300;
  font-style: italic;
}
.card-review-comment {
  margin-top: 1rem;
  line-height: 1.6;
}

.contact-form {
  max-width: 60rem;
  padding: 0 1rem;
  margin: 1rem auto 0;
}
@media (min-width: 576px) {
  .contact-form .contact-section-up-first,
.contact-form .contact-section-up-second {
    display: flex;
    gap: 1rem;
  }
}

@media (min-width: 576px) {
  .fields {
    flex: 1;
  }
}
.fields label {
  margin-bottom: 0.5rem;
  display: block;
  text-transform: capitalize;
  font-weight: 700;
}
.fields input, .fields textarea {
  width: 100%;
  margin-bottom: 1rem;
  padding: 0.5rem;
  font-size: 1.2rem;
  color: #fff2f2;
  border: none;
  border-radius: 0.5rem 0.2rem 0.5rem;
  background-color: #666;
  appearance: none;
}
.fields input::placeholder, .fields textarea::placeholder {
  color: #fff2f2;
}
.fields textarea {
  resize: none;
  height: 10rem;
}

.contact-submit {
  display: block;
  width: fit-content;
  margin: 0 auto;
  padding: 0.5rem 1.5rem;
  color: #fff2f2;
  font-weight: 700;
  border-radius: 0.3rem;
  border: 0.1rem solid #fff2f2;
  background-color: transparent;
  transition: all 0.3s ease-in-out;
}
.contact-submit:hover {
  width: 30%;
  background: #666;
}

.footer {
  min-height: 50vh;
  padding: 2rem;
  background-color: #000000;
}
.footer .footer-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 2rem;
  max-width: 90rem;
  margin: 0 auto;
}
.footer .list-section {
  text-transform: capitalize;
}
.footer .list-section h3 {
  margin-bottom: 1rem;
}
.footer .list-section .list-details {
  display: grid;
  gap: 1rem;
}
.footer .list-section .list-details li {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: transform 0.3s ease-in-out;
}
.footer .list-section .list-details li:hover {
  transform: scale(1.1);
}
.footer .list-section .list-details li img {
  width: 1.5rem;
}

